<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/shopManage/list">店铺列表</cms-tab>
      <cms-tab to="/shopManage/enterApplication">入驻申请</cms-tab>
    </cms-tabs>
    <cms-prompt collapse="true">
      <li>显示平台入住店铺申请信息，可进行审核操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>商家账号</label>
        <el-input v-model="queryParams.storeUsername" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>店铺等级</label>
        <el-select v-model="queryParams.levelId" @change="query">
          <el-option label="全部" value></el-option>
          <el-option
            v-for="item in gradeList"
            :key="item.id"
            :label="item.levelName"
            :value="item.id"
          ></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>店铺类型</label>
        <el-select v-model="queryParams.storeType" @change="query">
          <el-option label="全部" value=" "></el-option>
          <el-option label="个人店铺" :value="1"></el-option>
          <el-option label="企业店铺" :value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>主营类目</label>
        <el-select v-model="queryParams.saleCateId" value-key="id" @change="query">
          <el-option label="全部" value></el-option>
          <el-option
            v-for="item in categoryItems"
            :key="item.id"
            :label="item.cateName"
            :value="item.id"
          ></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>申请时间</label>
        <cms-picker @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="待审核" value="1"></el-option>
          <el-option label="审核不通过" value="3"></el-option>
          <el-option label="待缴费" value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
      <el-dialog title="审核不通过" :visible.sync="dialogFormVisible">
        <el-form ref="form" :model="dialogForm" :rules="saveRules">
          <el-form-item label="原因" prop="remark" label-width="120px" :error="errorMessage.remark">
            <el-input type="textarea" v-model="dialogForm.remark" auto-complete="off" rows="6"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="noPass(dialogForm.id, 3, dialogForm.remark )">确 定</el-button>
        </div>
      </el-dialog>
    </cms-sticky>
    <el-table
      :data="tableData"
      stripe
      ref="dataTable"
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      style="width: 100%"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width="180"></el-table-column>
      <el-table-column prop="storeUsername" label="商家账号" min-width="160"></el-table-column>
      <el-table-column prop="auditResults" label="审核状态" min-width="160">
        <div slot-scope="scope">
          <span v-if="scope.row.auditResults === 1" class="info">待审核</span>
          <span v-if="scope.row.auditResults === 2" class="success">审核通过,待缴费</span>
          <span v-if="scope.row.auditResults === 3" class="error">审核不通过</span>
        </div>
      </el-table-column>
      <el-table-column prop="storeType" label="店铺类型" min-width="110">
        <div slot-scope="scope">
          <span v-if="scope.row.storeType === 1">个人店铺</span>
          <span class="success" v-else-if="scope.row.storeType === 2">企业店铺</span>
        </div>
      </el-table-column>
      <el-table-column prop="storeLevel.levelName" label="店铺等级" min-width="110"></el-table-column>
      <el-table-column prop="productCategory.cateName" label="主营类目" min-width="150"></el-table-column>
      <el-table-column prop="createTime" label="申请时间" min-width="170">
        <template slot-scope="scope">{{$moment(scope.row.createTime)}}</template>
      </el-table-column>
      <el-table-column prop="storeInfo.address" label="所在地" min-width="160"></el-table-column>
      <el-table-column prop="productCategory.bondPrice" label="保证金" min-width="110">
        <template slot-scope="scope">{{$cms.float(scope.row.productCategory.bondPrice)}}</template>
      </el-table-column>
      <el-table-column label="操作" width="280" fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button
            icon="edit"
            size="small"
            type="info"
            @click.stop="$routerLink('/shopManage/enterApplication/view','update',{id:scope.row.id,storeType:scope.row.storeType})"
          >查看</cms-button>
          <cms-button
            v-if="scope.row.auditResults === 1"
            icon="edit"
            size="small"
            type="info"
            @click.stop="$routerLink('/shopManage/enterApplication/audit','update',{id:scope.row.id,storeType:scope.row.storeType})"
          >审核</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import rules from '@/rules'
import list from '@/mixins/list'
import form from '@/mixins/form.js'
import { livepage, shopManageDelete } from '@/api/shop/shopManage.js'
import { gradeList } from '@/api/shop/shopLevel.js'
import { productCategoryList } from '@/api/product/category.js'
var defaultData = {
  storeName: '',
  levelId: '',
  storeType: '',
  saleCateId: '',
  startTime: '',
  endTime: '',
  status: '',
  storeUsername: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list, form],
  data() {
    return {
      gradeList: [],
      categoryItems: [], // 商品分类列表
      queryParams: Object.assign({}, defaultData),
      dialogFormVisible: false,
      dialogForm: {
        id: '',
        status: 3,
        remark: ''
      },
      saveRules: {
        remark: [rules.required('请填写不通过原因'), rules.len(1, 500)]
      }
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(shopManageDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(shopManageDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    getGradeList() {
      // 获取店铺等级列表
      gradeList({ size: 999999, page: 0 }).then(res => {
        if (res.code === 200) {
          this.gradeList = res.data.content
        }
      })
    },
    getMenuTree() {
      // 获取分类节点
      productCategoryList().then(res => {
        this.categoryItems = res.data
      })
    },
    getAll() {
      this.getGradeList()
      this.getMenuTree()
    }
  },
  created() {
    // 首次触发
    this.getTableData(livepage, this.queryParams)
    this.getAll()
  }
}
</script>

<style>
</style>



